import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user/user';
import { IServent } from '../models/home';
import { save } from '../store/actions/servent';
import Page from '../components/home/new-servent';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    homeIndex: state.AppUser.homeIndex
});

const mapDispatchToProps = (dispatch: any) => ({
    onSave: (data: IServent, user: IUser, homeIndex: number, scallback: (result: boolean) => void) => dispatch(save(data, user, homeIndex, scallback))
});

export const NewServent = connect(mapStateToProps, mapDispatchToProps)(Page);