import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, TouchableHighlight, TextInput, Image, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { styles } from "../../../styles/page";
import { styles as iconStyles } from "../../../styles/icon";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IBlocks } from '../../../models/store/blocks';
import { IBlock } from '../../../models/block';
import Images from '../../images';
import { COLOR } from '../../../constants';
import { isNN, onEnter } from '../../../util';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser
  user: IUser,
  blocks: IBlocks,
  onRemove: (id: string, blocks: Array<IBlock>, callback: (result: boolean) => void) => void,
};

interface IState{
  isLoading: boolean,
  isEdit: boolean,
  searchText: string,
  searchResult: Array<IBlock>,
  searchResultIndex: number
};

export default class Blocks extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isEdit: false,
      searchText: '',
      searchResult: [],
      searchResultIndex: 10
    }
    this.onSearch = this.onSearch.bind(this);
    this.onRemove = this.onRemove.bind(this);
  };

  componentWillUnmount() {};

  onSearch = () => {
    if(isNN(this.state.searchText) && this.props.blocks.isUpToDate && !this.props.blocks.isError && isNN(this.props.blocks.data)){
      let searchText = this.state.searchText.toLocaleLowerCase();
      this.setState({
        searchResult: this.props.blocks.data.filter(d => (d.name + '').toLocaleLowerCase().indexOf(searchText) >= 0),
        searchResultIndex: 10
      });
    }else{
      this.setState({
        searchResult: [],
        searchResultIndex: 10
      });
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.blocks.data, (result: boolean) => {
              this.setState({
                isLoading: false
              });
              if(result === true){
                this.onSearch();
                Toast.success('Home removed successfully.');
              }else{
                Toast.fail('Failed to remove home.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
      return(
        <main style={styles.content}>
          <Breadcrum data={[{title: 'Blocks', link: 'blocks'}]} />
          <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
            <Text className='fr pointer' onClick={() => this.props.history.push('edit-block/0')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
          </View>
          <View style={{...modelStyles.row, marginTop: 0, paddingTop: 0}}>
            <TextInput type='text' placeholder="Search Block" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele, display: 'inline', width: 'calc(100% - 40px)', marginRight: 10}} value={this.state.searchText} onChange={(event: any) => this.setState({searchText: event.target.value})}
            onKeyPress={(e) => {
              onEnter(e, this.onSearch);
            }} />
            <TouchableHighlight style={{height: 30, marginTop: 13}} onClick={this.onSearch}>
                <Image className='pointer' src={Images.search} style={{width: 30, height: 30}} />
            </TouchableHighlight> 
          </View>

          {isNN(this.state.searchResult) ?
          this.state.searchResult.slice(0, this.state.searchResultIndex).map((d: IBlock, i: number) => {
            return(
              <View key={'block' + i} style={styles.row}>
                <View style={styles.ele}>
                  <Text>{'Name: ' + d.name}</Text>         
                </View>
                <View style={styles.ele}>
                  <Text>{'Description: ' + (isNN(d.description) ? d.description : '')}</Text>         
                </View>
                <View style={styles.ele}>
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.props.history.push('edit-block/' + d._id);////TODO
                    // this.props.navigation.navigate('EditBlock', {id: d._id});
                  }}>
                    <Image src={Images.edit} style={iconStyles.editIcon} />
                  </TouchableHighlight>
                    <TouchableHighlight style={{}} onClick={() => {
                      this.onRemove(d._id);
                    }}>
                      <Image src={Images.delete} style={iconStyles.deleteIcon} />
                    </TouchableHighlight>
                </View>
              </View>
            )
          })
          :
          <Text style={{padding: 20}}>No block match with your search!!!</Text>
        }
        {(this.state.searchResult.length > this.state.searchResultIndex) &&
        <View style={styles.row}>
          <View style={{flex: 1, flexDirection: 'row', padding: 10}}>
            <Text style={{color: COLOR.SECONDARY, fontWeight: 'bold'}} onClick={() => 
            this.setState({
              searchResultIndex: this.state.searchResultIndex + 10
            })}>Show More</Text>
          </View>
        </View>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
      );
  }
};