import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { loadAll, approve, reject } from '../store/actions/visitor';

import { IVisitor } from '../models/home';
import Page from '../components/home/visitors';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    appUser: state.AppUser
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  dispatch(loadAll(appClientId, homeId, isActive, sCallback, fCallback)),
    onApprove: (id: string, callback: (result: boolean) => void) => dispatch(approve(id, callback)),
    onReject: (id: string, callback: (result: boolean) => void) => dispatch(reject(id, callback))
});

export const Visitors = connect(mapStateToProps, mapDispatchToProps)(Page);