import { actionTypes } from '../action-types';
import { IServent } from '../../models/home/servent';
import { IUser } from '../../models/user/user';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';

let controller: string = 'servent';

export const loadAllByHome = (homeId: string, sCallback: (result: Array<IServent>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's/' + homeId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback(isNN(result.message) ? result.message : 'Failed to load Members.');
    }
  });
};

export const save = (data: IServent, user: IUser, homeIndex:number, callback: (result: boolean) => void) => (dispatch: any) => {
  // update
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        let servents = user.homes[homeIndex].servents;
        var foundIndex = servents.findIndex(d => d._id === result.data);
        servents[foundIndex]._id = result.data;
        servents[foundIndex].name = data.name;
        servents[foundIndex].age = data.age;
        servents[foundIndex].gender = data.gender;
        servents[foundIndex].mobileNo = data.mobileNo;
        servents[foundIndex].emailId = data.emailId;
           
        user.homes[homeIndex].servents = servents;
        dispatch(getUserResult(user as IUser));
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }else{ // add
    new Api().post(controller + '/' + user.homes[homeIndex]._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        data._id = result.data;
        data.createdBy = user.name;
        let servents = user.homes[homeIndex].servents;
        servents.push(data);
        user.homes[homeIndex].servents = servents;
        dispatch(getUserResult(user as IUser));
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }
};

export const remove = (id: string, user: IUser, homeIndex: number, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      user.homes[homeIndex].servents = user.homes[homeIndex].servents.filter((d, i) => d._id !== id);
      dispatch(getUserResult(user as IUser));
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getUserResult = (data: IUser) => ({
  type: actionTypes.LOAD_USER,
  payload: data
});