import * as React from 'react';
import { View, ScrollView, TextInput, Text } from '../../common/elements';
import Button from '../../common/button';
import RadioButton from '../../common/radio-button';
import { IHomes } from '../../../models/store/homes';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import { isNN, generateUUID } from '../../../util/index';
import { COLOR } from '../../../constants';
import Toast from  '../../../util/toast';
import { IHome, IVisitor } from '../../../models/home';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  gateNo: string,
  onToggleLoading: (isLoading: boolean) => void,
  onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, scallback: (result: boolean) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void
};

interface IState {
  homeId: string,
  id: string,
  type: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string,
  fileName: string,
  showHomes: boolean,
  homeSearchText: string
};

export default class NewVisitor extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      homeId: '',
      id: '',
      type: 'VISITOR',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      fileName: '',
      showHomes: false,
      homeSearchText: ''
    }
    this.onSave = this.onSave.bind(this);
    this.onClear = this.onClear.bind(this);
    this.onImageSelect = this.onImageSelect.bind(this);
    this.viewAll = this.viewAll.bind(this);
  };
  
  onImageSelect = (event: any) => {
    let file = event.target.files[0];
    if(file && file.name){
      let arrFileName = file.name.split('.');
      if(arrFileName && arrFileName.length > 1){
        let fileName = generateUUID();
        fileName += '.' + arrFileName[arrFileName.length - 1];
        this.setState({
          fileName: fileName
        });
        let data = {
          file: file,
          type: file.type,
          name: fileName
        };
        this.props.onUpload(data, (result: boolean, message: string) => {
          if(!result){
            Toast.fail(message ? message : 'Failed to upload file.');
          }
        });
      }
    }
  };

  onSave = () => {
    if(isNN(this.props.gateNo) && this.state.showHomes === false && this.state.homeId.length > 0 && isNN(this.state.name) && isNN(this.state.fileName)){
      // this.props.onToggleLoading(true);
      let visitor = {
        _id: '',
        type: this.state.type,
        name: this.state.name,
        age: this.state.age,
        gender: this.state.gender,
        mobileNo: this.state.mobileNo,
        emailId: '',
        image: this.state.fileName,
        passCode: '',
        gateNo: this.props.gateNo
      } as IVisitor;
      this.props.onSave(this.state.homeId, visitor, this.props.user, this.props.appUser, (result: boolean) => {          
        // this.props.onToggleLoading(false);
        if(result === true){
          // this.props.navigation.navigation.goBack(null);
          // Toast.success('Visitor ' + name + ' saved successfully.');
        }else{
          Toast.fail('Failed to save visitor ' + this.state.name + '.');
        }
      });
      this.setState({
        homeId: '',
        id: '',
        type: 'VISITOR',
        name: '',
        age: 0,
        gender: '',
        mobileNo: '',
        fileName: '',
        showHomes: false,
        homeSearchText: ''
      });
      Toast.info('Visitor will be saved and notified to the home.');
    }else{
      Toast.warn('Please fill Gate Number, Home and Image before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onClear = () => {
    this.setState({ 
      homeId: '',
      id: '',
      type: 'VISITOR',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      fileName: '',
      showHomes: false,
      homeSearchText: ''
    });
  };

  viewAll = () => {
    if(isNN(this.props.gateNo)) {
      this.props.history.push('all-visitors')
    } else {
      Toast.warn('Please enter Gate Number to view this page.');
    }
  };

  render() {
    return(
        <View style={styles.container}>
          <View style={modelStyles.row}>
            <Text className='fr pointer' onClick={this.viewAll} style={{...styles.ele, color: COLOR.SECONDARY, fontWeight: 'bold', textAlign: 'right'}}>View all Active Visitors</Text>
          </View>
          <View style={modelStyles.row}>
          <TextInput type='text' placeholder='Enter Home Number' className='form-control'
          style={{...modelStyles.ele, ...styles.ele,
            flexDirection: 'row',
            padding: 12,
            borderWidth: 1,
            borderColor: COLOR.HR_COLOR,
            borderRadius: 5
          }} 
          value={this.state.homeSearchText} 
          onChange={(event: any) => this.setState({showHomes: true, homeSearchText: event.target.value})} />
          </View>
          {
            (this.state.showHomes && isNN(this.state.homeSearchText) && this.props.homes && this.props.homes.data.length > 0 ) &&
            <ScrollView style={{...modelStyles.row, width: '100%', overflow: 'scroll'}}>
              {
                this.props.homes.data.filter((d, i) => d.number.toLowerCase().indexOf(this.state.homeSearchText.toLowerCase()) >= 0).map((d: IHome, i: number) => {
                  return(
                    <Text 
                    className='pointer'
                    key={'ho' + i}
                    style={{...styles.ele, flexDirection: 'column', margin: 3, padding: 6, backgroundColor: COLOR.CARD_UNDERLAY}}
                    onClick={() => {
                      this.setState({homeSearchText: d.number, homeId: d._id, showHomes: false});
                    }}>{d.number}</Text>
                  )
                })
              }
            </ScrollView>
          }
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.type === 'VISITOR'} text='Visitor' onPress={() => this.setState({type: 'VISITOR'})} />
            <RadioButton isActive={this.state.type === 'DELIVERY'} text='Delivery' onPress={() => this.setState({type: 'DELIVERY'})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Name" className='form-control' style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder='Age' className='form-control' maxLength={3} style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
            <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder='Mobile Number' className='form-control' style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
          </View>    
          <View style={modelStyles.row}>
              <input type='file' name='image' placeholder='SELECT IMAGE' onChange={this.onImageSelect}/>
              {isNN(this.state.fileName) &&
              <Text style={{...styles.ele, marginLeft: 15}}>{this.state.fileName}</Text>
              }
          </View>
          <View className='center' style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
              <Button
              title="SAVE"
              onPress={this.onSave}
              style={{...styles.ele, marginRight: 10}}
              />
              <Button
              title="CLEAR"
              onPress={this.onClear}
              style={styles.ele}
              />
          </View>
        </View>
    );
  }
};

const styles = {
  container:{
    width: '100%',
    paddingLeft: 15,
    paddingRight: 15,
  },
  ele: {
    fontSize: 17
  }
};